import { d as private_env } from "../../chunks/shared-server.js";
const animeQuery = "query($season:MediaSeason,$seasonYear:Int $nextSeason:MediaSeason,$nextYear:Int){trending:Page(page:1,perPage:6){media(sort:TRENDING_DESC,type:ANIME,isAdult:false){...media}}season:Page(page:1,perPage:6){media(season:$season,seasonYear:$seasonYear,sort:POPULARITY_DESC,type:ANIME,isAdult:false){...media}}nextSeason:Page(page:1,perPage:6){media(season:$nextSeason,seasonYear:$nextYear,sort:POPULARITY_DESC,type:ANIME,isAdult:false){...media}}popular:Page(page:1,perPage:6){media(sort:POPULARITY_DESC,type:ANIME,isAdult:false){...media}}top:Page(page:1,perPage:12){media(sort:SCORE_DESC,type:ANIME,isAdult:false){...media}}}fragment media on Media{id title{userPreferred}coverImage{extraLarge large color}startDate{year month day}endDate{year month day}bannerImage season seasonYear description type format status(version:2)episodes duration chapters volumes genres isAdult averageScore popularity mediaListEntry{id status}nextAiringEpisode{airingAt timeUntilAiring episode}studios(isMain:true){edges{isMain node{id name}}}}";
const animeVariables = {
  type: "ANIME",
  season: "SUMMER",
  seasonYear: 2024,
  nextSeason: "FALL",
  nextYear: "2024"
};
const animeOptions = {
  method: "post",
  headers: {
    "Content-Type": "application/json",
    accept: "application/json"
  },
  body: JSON.stringify({
    query: animeQuery,
    variables: animeVariables
  })
};
const load = async ({ platform }) => {
  const cacheKey = "anime_data";
  const cacheTTL = 3600;
  const apiCache = platform?.env.API_CACHE;
  if (!apiCache) {
    throw new Error("API_CACHE is not defined in the platform environment");
  }
  const cachedAnimeData = await apiCache.get(cacheKey);
  if (cachedAnimeData) {
    return JSON.parse(cachedAnimeData);
  }
  try {
    const response2 = await fetch(platform.env.BASE_URL, animeOptions);
    if (!response2.ok) {
      throw new Error(`Failed to fetch data: ${response2.statusText}`);
    }
    const animeData2 = await response2.json();
    await apiCache.put(cacheKey, JSON.stringify(animeData2), {
      expirationTtl: cacheTTL
    });
    return animeData2.data;
  } catch (error) {
  }
  const response = await fetch(private_env.BASE_URL, animeOptions);
  const animeData = await response.json();
  await apiCache.put(cacheKey, JSON.stringify(animeData), {
    expirationTtl: cacheTTL
  });
  return animeData.data;
};
export {
  load
};
